import React from "react";
import { Row, Col } from "reactstrap";
import Banner from "./Banner";
import RevenueChart from "../ui-elements/cards/analytics/Revenue";
import GoalOverview from "../ui-elements/cards/analytics/GoalOverview";
import BrowserStats from "../ui-elements/cards/analytics/BrowserStatistics";
import ClientRetention from "../ui-elements/cards/analytics/ClientRetention";
import SessionByDevice from "../ui-elements/cards/analytics/SessionByDevice";
import CustomersChart from "../ui-elements/cards/analytics/Customers";
import ChatWidget from "../../components/@vuexy/chatWidget/ChatWidget";
import "swiper/css/swiper.css";
import { Users, CreditCard, Package, Server } from "react-feather";
import "../../assets/scss/plugins/extensions/swiper.scss";

import "../../assets/scss/plugins/charts/apex-charts.scss";
import { toast } from "react-toastify"
import "react-toastify/dist/ReactToastify.css"
import { ToastContainer } from "react-toastify"
import "../../assets/scss/plugins/extensions/toastr.scss"
import DashboardCards from "./DashboardCards";
import {
  revenueGenerated,
  revenueGeneratedSeries,
  subscribersGained,
  subscribersGainedSeries,
  ordersReceived,
  ordersReceivedSeries,
  quaterlySales,
  quaterlySalesSeries,
} from "./CardData";

let $primary = "#7367F0",
  $success = "#28C76F",
  $danger = "#EA5455",
  $warning = "#FF9F43",
  $primary_light = "#9c8cfc",
  $warning_light = "#FFC085",
  $danger_light = "#f29292",
  $stroke_color = "#b9c3cd",
  $label_color = "#e7eef7";

class EcommerceDashboard extends React.Component {
  state = {};
  componentDidMount() {
    if(localStorage.getItem('logInGreeting')) {
      toast.success('Good to see you back !!!!!!!!');
      localStorage.removeItem('logInGreeting');
    }
  }
  render() {
    return (
      <React.Fragment>
        <ToastContainer />
        <Row className="match-height" style={{marginTop: '-75px'}}>
          <Col sm="12" style={{width:'118%', marginLeft: window.screen.width < 500 ? '-29px' : '-2px'}}>
            <Banner />
          </Col>
    
          {/* <Col lg="3" md="6" sm="6" onClick={event =>  window.location.href='/#/assets/add'}> */}
          <Col sm="12">
            <div style={{display:'flex', maxWidth: '100%', flexDirection: window.screen.width < 500 ? 'column' : 'row'}}>
          <div style={{display:'flex', flexDirection:'row', justifyContent: 'center'}}>
         
            <div onClick={() => {this.props.history.push("/assets/add")}}>
          <DashboardCards
            icon={<Package color="white" className="primary" size={22} />}
            iconBg="white"
            stat="Add Assets"
            statTitle="Bank details, FD, RD, Insurance, Properties etc."
            options={subscribersGained}
            series={subscribersGainedSeries}
            type="area"
            hideChart={window.screen.width < 500 ? true : false}
          />
          </div>
            {/* <SubscribersGained /> */}
         
          <div onClick={() => {this.props.history.push("/liability/add")}}>
          <DashboardCards
            icon={<Server color="white" className="danger" size={22} />}
            iconBg="white"
            stat="Add Liabilties"
            statTitle="Loan, EMI, Cash borrowed etc with collateral details"
            options={quaterlySales}
            series={quaterlySalesSeries}
            type="area"
			      hideChart={window.screen.width < 500 ? true : false}
          />
          </div>
          </div>
          <div style={{display:'flex', justifyContent: 'center',flexDirection:'row'}}>
          <div onClick={() => {this.props.history.push("/portfolio")}}>
          <DashboardCards
            icon={<CreditCard color="rgb(222,222,224)" className="success" size={22} />}
            iconBg="white"
            stat="View Portfolio"
            statTitle="View and analyze your portfolio of saved assets and liabilties"
            options={revenueGenerated}
            series={revenueGeneratedSeries}
            type="area"
			      hideChart={window.screen.width < 500 ? true : false}
          />
          </div>

          <div onClick={() => {this.props.history.push("/nominee/list")}}>
          <DashboardCards
            icon={<Users color="white" className="warning" size={22} />}
            iconBg="white"
            stat="Manage Nominees"
            statTitle="Add/Edit your nominees against your assets & liabities"
            options={ordersReceived}
            series={ordersReceivedSeries}
            type="area"
            hideChart={window.screen.width < 500 ? true : false}
          />
          </div>
          </div>
          </div>
          </Col>
        </Row>
        <Row className="match-height">
          <Col lg="8" md="6" sm="12">
            <RevenueChart
              primary={$primary}
              dangerLight={$danger_light}
              strokeColor={$stroke_color}
              labelColor={$label_color}
            />
          </Col>
          <Col lg="4" md="6" sm="12">
            <GoalOverview strokeColor={$stroke_color} success={$success} />
          </Col>
        </Row>
        <Row className="match-height">
          <Col lg="4" md="6" sm="12">
            <BrowserStats />
          </Col>
          <Col lg="8" md="6" sm="12">
            <ClientRetention
              strokeColor={$stroke_color}
              primary={$primary}
              danger={$danger}
              labelColor={$label_color}
            />
          </Col>
        </Row>
        <Row>
          <Col lg="4" md="12">
            <SessionByDevice
              primary={$primary}
              warning={$warning}
              danger={$danger}
              primaryLight={$primary_light}
              warningLight={$warning_light}
              dangerLight={$danger_light}
            />
          </Col>
          <Col lg="4" md="12" className="text-center align-middle">
            <ChatWidget></ChatWidget>
          </Col>
          <Col lg="4" md="12" className="text-center align-middle">
            <CustomersChart
              primary={$primary}
              warning={$warning}
              danger={$danger}
              primaryLight={$primary_light}
              warningLight={$warning_light}
              dangerLight={$danger_light}
            />
          </Col>
        </Row>
      </React.Fragment>
    );
  }
}

export default EcommerceDashboard;
